/* CSS for the TextArea */

.heading{
    margin-top: 20px;
    text-align: center;
}

#text{
    width: 40rem;
    height: 20rem;
    color: black;
    margin-left: 30vw;
    margin-top: 20px;
    background-color: transparent;
    border: 1px solid white;
    padding: 6px 6px;
}

.btn{
    width: 140px;
    height: 50px;
    border-radius: 8px;
    border: 1px solid white;
    cursor: pointer;
    margin-left: 32vw;
    margin-top: 2px;
}

.green{
    background-color: green;
}

.blue{
    background-color: blue;
    margin-left: 10px;
}

.red{
    background-color: red;
    margin-left: 10px;
}

.purple{
    background-color: purple;
    margin-left: 10px;
}

.summary{
    margin-top: 10px;
    text-align: center;
}

#phead{
    margin: 8px 8px;
    text-decoration: underline;
}

#pview{
    line-height: 20px;
    padding: 0 10px;
    color: black;
}

/* re-designing page for pixel < 950 */

@media(max-width:950px)
{
    #text{
        width: 20rem;
        height: 20rem;
        margin-left: 20px;
    }

    .btn{
        width: 80px;
        height: 40px;
        border-radius: 8px;
        margin-left: 20px;
        margin-top: 4px;
    }
}

/* re-designing for pixel < 670 */

@media(max-width:670px)
{
    #text{
        width: 20rem;
        height: 10rem;
        margin-left: 15px;
    }

    .btn{
        width: 80px;
        height: 40px;
        margin-left: 19vw;
        margin-top: 8px;
    }
}