/* css reset */
*{
    margin: 0;
    padding: 0;
    color: #fff;
}
body{
    background-color: rgb(116, 108, 108);
}

.header{
    top: 0;
    left: 0;
    position: sticky;
    /* background-color: black; */
    display: flex;
    justify-content: space-between;
    /* border: 2px solid blue; */
}

.header::before{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    background-color: black;
    top: 0;
    left: 0;
    opacity: 0.5;
} 

.nav{
    display: inline-flex;
    align-items: center;
}

.nav ul{
    display: flex;
    /* background-color: black; */
    align-items: center;
    /* border: 2px solid red; */
}

.nav ul li{
    list-style: none;
    margin: 0 2.4rem;
}

.nav ul li a{
    text-decoration: none;
    color: #fff;
    font-size: 1.4rem;
    padding: 4px 14px;
    text-transform: uppercase;
}

.nav ul li a:hover{
    background-color: white;
    color: black;
    border-radius: 13px;
}

/* logo */

.logo{
    display: flex;
    color: #fff;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 14px;
    /* border: 2px solid red; */
    cursor: pointer;
}


/* css for the hamburger menu */

#hamburger{
    width: 2rem;
    height: 1.3rem;
    margin: 1.5rem 0 1rem 1rem;
    cursor: pointer;
    z-index: 100;
    display: none;
    /* border: 2px solid blue; */
}

.bar{
    height: 5px;
    width: 100%;
    background-color: #fff;
    display: block;
    border-radius: 5px;
    transition: 0.3s ease;
}

#bar1{
    transform: translateY(-4px);
}

#bar3{
    transform: translateY(4px);
}

.icon .bar{
    background-color: #fff;
}

.icon #bar1{
    transform: translateY(4px) rotate(-45deg);
}

.icon #bar3{
    transform: translateY(-6px) rotate(45deg);
}

.icon #bar2{
    opacity: 0;
}

.change{
    background-color: black;
    height: 100vh;
    margin: 0;
    position: absolute;
    font-size: 30px;
    top: 0;
    z-index: -100;
    width: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.change ul{
    display: flex;
    flex-direction: column;
    /* border: 2px solid yellow; */
    justify-content: space-evenly;
    align-items: center;
    height: 50vh;
    margin-top: 20vh;
}

.change ul li a{
    font-size: 1rem;
}

/* re-designing page for pixel < 950 */

@media(max-width:950px)
{

    .nav ul li{
        margin: 0 1.6rem;
    }

    .nav ul li a{
        font-size: 1.1rem;
        padding: 2px 6px;
    }
    
    .nav ul li a:hover{
        border-radius: 6px;
    }

    .logo .short{
        font-size: 1.4rem;
    }
    .logo .full{
        font-size: 1rem;
    }

}

/* re-designing for pixel < 670 */

@media(max-width:670px){

    #hamburger{
        display: block;
    }

    #sidebar{
        display: none;
    }

    #text{
        width: rem;
        height: 20rem;
        background-color: transparent;
        border: 1px solid white;
    }
}


